body {
  margin: 0;
  font-size: 15px;
  line-height: 30px;
  font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
